import React, { createContext, useState, useEffect } from "react";
import axios from "axios";

// Create the context
const DataContext = createContext();

// Custom data provider component
const DataProvider = ({ children }) => {
  const [data, setData] = useState([]);

  useEffect(() => {
    // Fetch the data from the API using axios (Replace 'apiUrl' with the actual API endpoint)
    axios
      .get(
        "https://script.googleusercontent.com/macros/echo?user_content_key=w4lt1OfIX9G5ucmwGvR88lnjuh4rWpdV8Ayii8jJxN7ZqIGd_cUhtj2HicFQshgRXRcwfP6SHyUFndOqXT84p95zAjSB6b5om5_BxDlH2jW0nuo2oDemN9CCS2h10ox_1xSncGQajx_ryfhECjZEnNvl4MpQDqUbGB3tdq46bSt_tabduSrZfvIRtzAanYmvxwgAFM_mv9cBhpQFkpwQfO81yfZG3MEU4iOpsRjxF98TN2v-8JuIEtz9Jw9Md8uu&lib=MSZ1xG1TXGp31k2VJ_D0gUj5Xolqlovhj"
      ) // Replace 'apiUrl' with the actual API endpoint
      .then((response) => {
        console.log("API Response:", response.data); // Check the API response in the console
        setData(response.data.data); // Assuming the data is available as 'data' property in the API response
      })
      .catch((error) => {
        console.error("Error fetching data:", error);
      });
  }, []);

  return (
    <DataContext.Provider value={{ data }}>{children}</DataContext.Provider>
  );
};

export { DataContext, DataProvider };
